import Bus  from './bus'
import Aux from '../common/auxx'
import VPrintTemplate from './printTemplate'
export default {
    name :'VPrintTemplate',
    components :{ VPrintTemplate },
    props :{
        // 解析的JOSN值
        value :{
            type: [Object, Array],
        },
        // 是否显示注解
        note :{
            type :[Boolean],
            default :false
        },
        // 是否可以修改注解
        unote :{
            type :[Boolean],
            default :false
        },
        // 每是数组的子集是否只显示一条数据
        aone :{
            type :[Boolean],
            default :false
        },
        // 是否可以修改data ,(这里的data 是键值中的值); 
        udata :{
            type :[Boolean],
            default :false
        },
        // 是否显示data
        idata :{
            type :[Boolean],
            default :true
        },
        // 是否修改data可选项
        odata :{
            type :[Boolean],
            default :false
        },
        // 自动化数据 
        mock :{
            type :[Boolean],
            default :false
        },
        // 超出部分隐藏
        overflow :{
            type:[Boolean],
            default :true
        }
    },
    data() {
        return {
            list :[],
            parent :{},
            mock_mode :[
                {label: '普通模式',value: 'default'},
                {label: '高级模式',value: 'high'},
            ],
            data_status :[
                { label :'必填' ,value :'default' },
                { label :'选填' ,value :'optional' },
                { label :'不填' ,value :'not' }
            ]
        }
    },
    created(){
        this.list = this.value.list || [];
        this.parent = this.value.parent;
        if(this.aone){
            this.list = Aux.aone(this.list);
        }
    },
    methods :{
        getMockMenu :function(){
            return Aux.mockRandom();
        },
        getDataStatusStyle :function(state){
            var dataStyle = { };
            switch(state){
                case 'optional' :
                        dataStyle = {
                            color : '#027BE3',
                        }
                    break;
                case 'not' :
                        dataStyle = {
                            color : '#9e9e9e',
                            textDecoration :'line-through'
                        }
                    break;
                default :
                        dataStyle = {
                            color : '#0b7500',
                        }
                    break;
            }
            return dataStyle;
        },
        tapKey :function(item){
            Bus.$emit('tapKey',JSON.parse(JSON.stringify(item)))
        },
        
    }

}
